*{
    margin: 0px;
    padding: 0px;
    box-sizing: border-box;
    vertical-align: middle;
  }
  
  body{
    background-color: #fffbfb;
  }
  .main-container{
    text-align: center;
    width: 270px;
    margin: auto;
    margin-top: min(10% , 70px) ;
    border: solid cadetblue;
    border-radius: 15px;
    overflow: hidden;
    box-shadow: -5px 6px 4px 2px rgb(73 67 67 / 50%);
  }
  h1{
    display: inline;
  }
  .eachItems{
    display: flex;
    justify-content: space-between;
    background-color: blueviolet;
    padding: 2px;
    border: solid;
    border-color: blue;
    border-radius: 15px;
    margin: 3px;
    transition: 0.1s;
  }
  .eachItems:hover {
    color: white;
    border-color: white;
  }
  .eachItems svg:hover{
    color: red;
  }
  .regain{
    color: rgb(0, 247, 0) ;
  }
  .eachItems div {
    padding: 7px;
    word-break: break-word;
  }
  .done{
    text-decoration: line-through;
    text-decoration-color: tomato;
    text-decoration-thickness: 2px;
  }
  .nav{
    background-color: blue;
    height: 40px;
  }
  .add{
    background-color: white;
    padding: 8px;
    justify-content: space-around;
    height: 58px;
  }
  
  .notes{
    text-align: left;
    height: 300px;
    width: 100%;
    background-color: aqua;
    overflow-y: auto ;
  }
  
  .notes svg{
      align-self: center;
  }
  #remove{
    width: auto;
    background-color: aqua;
    padding: 5px 0px;
    height: 50px;
  }
  #remove button:hover {
    background-color: red ;
  }
  
  ::-webkit-scrollbar  {
    
    height: 5px;
    width: 5px;
    background-color: rgba(20, 18, 18, 0.7);
    overflow: hidden;
  }
  ::-webkit-scrollbar-thumb{
    background-color: yellow;
    background-clip: padding-box;
  }
  .notes::-webkit-scrollbar-corner {
    background-clip: padding-box;
    background-color: #f5f5f5;
    border: solid #fff;
    border-width: 3px 0 0 3px;
    -webkit-box-shadow: inset 1px 1px 0 rgb(0 0 0 / 14%);
    box-shadow: inset 1px 1px 0 rgb(0 0 0 / 14%);
  }
  ::-webkit-scrollbar-corner {
    background: transparent;
  }
  
  ::-webkit-scrollbar-corner {
    background: transparent;
  }
  .notes::-webkit-scrollbar-thumb {
    border-width: 1px 1px 1px 2px;
  }
  ::-webkit-scrollbar-thumb {
    /* background-color: rgba(0,0,0,.2); */
    background-clip: padding-box;
    border: solid transparent;
    border-width: 1px 1px 1px 6px;
    min-height: 28px;
    padding: 100px 0 0;
    -webkit-box-shadow: inset 1px 1px 0 rgb(0 0 0 / 10%), inset 0 -1px 0 rgb(0 0 0 / 7%);
    box-shadow: inset 1px 1px 0 rgb(0 0 0 / 10%), inset 0 -1px 0 rgb(0 0 0 / 7%);
  }
  @media (max-width:350px){
    .main-container{
      width: 100vw;
    }
    .notes{
      height: calc( 100vh - 148px);
    }
    .main-container{
      margin: 0;
      border-radius: 0;
    }
    ::-webkit-scrollbar  {
    
      height: 0px;
      width: 0px;
    }
  }